import React from 'react';
import AboutComp from './AboutComp';

function About(){
    return(
       <div className="about-wrapper"> {/*++++ ABOUT US PAGE CONTENT STARTS HERE ++++*/}
           <div className="about-main-wrapper">
               <p>HOW IT ALL STARTED</p>
               <h3>The Story Behind</h3>
           </div>
           <div className="about-content">
               <p>It all started in 2016 when one of my friends introduced me to Front End development. We started learning HTML, and quickly after it, I started learning CSS, at that point in time I was mind blown by the power of CSS!</p>
               <p>After a few months, I started helping my friends by providing them websites for their businesses, so that they can increase their business and revenue. Some of my friends liked my work so much that they introduced me to the top business leaders of the town.</p>
               <p>I started learning jQuery to increase my "web development skills", but after some time, I realized that jQuery is built on top of JavaScript, Then I started learning JavaScript, and when I understood what is going on under the hood, I started React JS to help me on the long run on the front-end. I also want to learn NodeJs in the future so that I can become a full-stack development and can add value to the company and projects on which I will be working. :)</p>
               <div className="improvement">
                  <h3>Improving the WEB since 2016</h3>
                  <p>As I have mentioned above, I'm specialized in HTML, CSS, and JavaScript on the frontend, but I'm pretty comfortable with jQuery, ReactJs, WordPress. If you want more information about the projects which I have done to date, please visit the Projects page of my portfolio.</p>
                  <p>See the Technologies and Projects page to know more about my expertise.</p>
                  <p>Get in touch with me and I'll bring your online business and website to the next level!</p>
               </div>
               <div class="expertise">
                 <div class="row">
                   <AboutComp aboutClass="web-dev" aboutImg="assets/images/web.png" aboutTitle="WEB DEVELOPMENT" aboutDesc="HTML, CSS, Jquery, JavaScript, React Js. You name it. I have it all covered!"/>

                   <AboutComp aboutClass="web-dev" aboutImg="assets/images/mobile.png" aboutTitle="RESPONSIVE DESIGN" aboutDesc="In 21st Century, you can't take your business online without a mobile responsive website. Let me make a responsive website for you!"/>

                   <AboutComp aboutClass="web-dev" aboutImg="assets/images/responsive.png" aboutTitle="PSD TO HTML" aboutDesc="Do you have a nice design in adobe photoshop or even invision? get in touch and I'll make them fully responsive, nice looking websites!"/>

                   <AboutComp aboutClass="web-dev no-margin" aboutImg="assets/images/wordpress.png" aboutTitle="WORDPRESS" aboutDesc="Billions of website and Web Apps are built on top of Wordpress because it's easy to use, yours can become the next one!"/>
                 </div>
              </div>
          </div>
      </div>
    );
}

export default About;