import React from 'react';
function HeroBanner(){
    return(
        <>
          <div className="hero-banner">
              <div className="tagline-wrapper">
                  <div className="main-wrapper">
                      <p>PRASHANT SHAKYA</p>
                      <h2>FRONT-END DEVELOPER</h2>
                      <q>Choose a job you love, and you will never have to work a day in your life.</q>
                      <p className="writer"><em>― Confucius</em></p>
                  </div>
              </div>
          </div>
        </>
    );
}
export default HeroBanner;