import React from 'react';

function AboutComp(props) {
  return (
      <div className="col-lg-3 col-md-6 col-sm-6 col-xs-12">
        <div className={props.aboutClass}>
           <img src={props.aboutImg}></img>
           <p>{props.aboutTitle}</p>
           <p>{props.aboutDesc}</p>
        </div>
      </div>
  )
}

export default AboutComp;