import React from 'react';
function ProjectsComp(props) {
    return (
    <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
        <div className="project-info">
            <img src={props.projectImg}/>
            <div className="project-content">
                <p>{props.projectName}</p>
                <input type="text" value={props.projectLink} readOnly/>
                <div className="tags">
                    <span>{props.Tag1}</span>
                    <span>{props.Tag2}</span>
                    <span>{props.Tag3}</span>
                    <span>{props.Tag4}</span>
                </div>
            </div>
            <div className="project-tooltip">
                <a href={props.projectURL} className="view-project" target="_blank">VIEW PROJECT</a>
            </div>
        </div>
    </div>
    )
}

export default ProjectsComp;