import React from 'react';

function TechnologyComp(props) {
    return (
        <div class="col-lg-4 col-md-6 col-sm-6 col-xs-12">
        <div className={props.TechClass}>
           <img src={props.TechImg}/>
           <p><span>{props.Technology}</span></p>
        </div>
      </div>
    )
}

export default TechnologyComp;