import React from 'react';
import ContactComp from './contactComp';

class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: "",
            email: "",
            phone: "",
            company: "",
            message: ""
        }
        this.handleName = this.handleName.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePhone = this.handlePhone.bind(this);
        this.handleCompany = this.handleCompany.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleName(e) {
        console.log(e.target.value);
        this.setState({
            name: e.target.value
        })
    }

    handleEmail(e) {
        this.setState({
            email: e.target.value
        })
    }

    handlePhone(e) {
        this.setState({
            phone: e.target.value
        })
    }

    handleCompany(e) {
        this.setState({
            company: e.target.value
        })
    }

    handleMessage(e) {
        this.setState({
            message: e.target.value
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        const URL = "https://docs.google.com/forms/u/0/d/e/1FAIpQLSfrJ9QSU_zyqQW1r2ejK4IPZzRrU0voG9OulZ7lhzzZlogGOA/formResponse";
        var form = new FormData();
        form.append("entry.636787634",this.state.name)
        form.append("entry.445025399", this.state.phone)
        form.append("entry.1541669671", this.state.company)
        form.append("entry.1723364748", this.state.message)
        form.append("emailAddress", this.state.email)
        
        fetch(URL, {
            method: 'POST',
            // mode: 'cors',
            // redirect: 'follow', // manual, *follow, error
            // referrerPolicy: 'no-referrer', //
            headers: {
                //'Content-Type': 'application/json'
                'Content-Type': 'text/xml'
                //'Content-Type': 'application/x-www-form-urlencoded',
            },
            // dataType:'json',
            body: {
                "entry.636787634":this.state.name,
                "entry.445025399": this.state.phone,
                "entry.1541669671": this.state.company,
                "entry.1723364748": this.state.message,
                "emailAddress": this.state.email
            }
            // body: form
        }).then(resp => {
            return resp.json()
        }).then(resp => {
            console.log(resp);
        })
    }
    render() {
        return (
            <div className="project-data-wrapper">
                <div className="data-wrapper-contact">
                    <h2>FEEL FREE TO CONTACT</h2>
                    <p>If you have a nice idea of a product or you want to have a website for you firm or you want to take you business online, then feel free to get in touch with me. I will make sure to give you best of the best.</p>
                </div>
                <div className="project-wrapper contact-wrapper">
                    <p><strong>SEND YOUR</strong> MESSAGE</p>
                    <p>If you have question or would like more information on our works, Please feel free to contact me, we will discuss and do something productive.</p>
                    <form action="https://docs.google.com/forms/u/0/d/e/1FAIpQLSfrJ9QSU_zyqQW1r2ejK4IPZzRrU0voG9OulZ7lhzzZlogGOA/formResponse" method="POST">
                        <div className="row">
                            <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                <div className="form-group">
                                    <label for="name">Name *</label>
                                    <input type="text" name="entry.445025399" className="form-control" id="name" placeholder="Enter Name" value={this.state.name} required onChange={this.handleName} />
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                <div className="form-group">
                                    <label for="email">Email *</label>
                                    <input type="text" name="entry.636787634" className="form-control" id="email" placeholder="Enter Email" value={this.state.email} required onChange={this.handleEmail} />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                <div className="form-group">
                                    <label for="mobile">Mobile Number *</label>
                                    <input type="text" name="entry.1541669671" className="form-control" id="mobile" placeholder="Mobile Number" value={this.state.phone} required onChange={this.handlePhone} />
                                </div>
                            </div>
                            <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                <div className="form-group">
                                    <label for="company">Company Name *</label>
                                    <input type="text" name="entry.1723364748" className="form-control" id="company" placeholder="Company Name" value={this.state.company} required onChange={this.handleCompany} />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                <div className="form-group">
                                    <label for="message">Message *</label>
                                    <textarea rows="4" type="text" className="form-control" id="message" placeholder="Enter Your Message" required onChange={this.handleMessage}>{this.state.message}</textarea>
                                </div>
                            </div>
                        </div>
                        <button type="submit" onClick={this.handleSubmit}>SEND MESSAGE</button>
                    </form>
                    <div className="row">
                        <ContactComp contactClass="contact-page" contactIcon="fa fa-volume-control-phone" contactContent="7017720398" />
                        <ContactComp contactClass="contact-page" contactIcon="fa fa-envelope" contactContent="Shakyap6@gmail.com" />
                        <ContactComp contactClass="contact-page no-margin" contactIcon="fa fa-linkedin-square" contactContent="Connect On linkedIn" />
                    </div>
                </div>
            </div>
        )
    }
}

export default Contact;